import { Component, OnInit } from '@angular/core';
import { PusherService } from '../../services/pusher.service';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {

  likes: any =  10;
  canSendMsg = true
  canLike = true
  user = JSON.parse(localStorage.getItem('user'));



  newMsg:string
  name:string
  messeges = []
  chat = 
  {
    name:null,
    msg:null,
    hour:null,
    minute:null
    
  }


  constructor(private pusherService: PusherService) { }

  ngOnInit() {

    console.log(this.user)

    this.pusherService.channel.bind('new-like', data => {
      console.log(data)
      this.likes = data.likes.likes ;
      this.likes = parseInt(this.likes, 10) + 1;
    });

    this.pusherService.channel.bind('new-msg', data => {
      console.log(data)
       this.chat = data.chat
       this.messeges.push(data.chat) ;
      });

  }


  liked() {
    this.canLike = false
    this.pusherService.like( this.likes );
    this.likeWait()
  }

  addMsg() {
    this.canSendMsg = false
    this.chat.msg = this.newMsg
    this.chat.name = this.name
    this.chat.hour = new Date().getHours()
    this.chat.minute = new Date().getMinutes()
    this.pusherService.msg(this.chat)  
    this.msgWait()
  }


  ////////// timeouts
  likeWait()
  {
    setTimeout(() => {
      console.log("after time out")
      this.canLike = true
    }, 1000);
  }

  msgWait()
  {
    setTimeout(() => {
      console.log("after time out")
      this.canSendMsg = true
    }, 1500);
  }

}
